.App {
  text-align: center;
  background-color: #2d724a;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #2d724a;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.App-link {
  color: #61dafb;
}

.vs-code-window {
  background-color: #1e1e1e;
  width: 90%;
  margin: 3%;
}

.vs-code-content {
  height: 80vh;
  display: flex;
}

.vs-bottom{
  background-color: #007ACC;
  display: flex;
  max-height: 18px;
  min-height: 26px;
}

.vs-bottom-left-1{
  text-align: left;
  width: 100px;
  float: left;
  padding-left: 10px;
  color: white;
}

.vs-bottom-left-2{
  text-align: left;
  width: 33px;
  float: left;
  padding-left: 10px;
  color: white;
}


.vs-bottom-left-1:hover{
  background-color: #1E87CE;
}
.vs-bottom-left-2:hover{
  background-color: #1E87CE;
}

.vs-bottom-icons{
  padding: 4px;
}

.MuiInputBase-root{
  border-radius: 0px !important;
  padding: 0px;
  color: white !important;
}

.vs-language-change{
  
  height: 26px;
}

.vs-bottom-right{
  float: right;
  width: 100%;
  text-align: right;
  color: white;
  padding-right: 10px;
}

.vs-icon-mini{
  max-width: 18px;
  max-height: 18px;
  display: inline-block;
  vertical-align: middle;
  filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(143deg) brightness(102%) contrast(101%);
}

.vs-code-top {
  background-color: #3c3c3c;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

.vs-icon {
  padding: 10px;
  max-width: 25px;
  max-height: 25px;
  filter: invert(56%) sepia(1%) saturate(1%) hue-rotate(317deg) brightness(93%) contrast(86%);
}

.vs-icon:hover {
  cursor: pointer;
  filter: invert(87%) sepia(1%) saturate(2773%) hue-rotate(258deg) brightness(110%) contrast(108%);
}

.vs-code-top div {
  width: 100%;
  font-family: arial, sans-serif;
}

.vs-code-tabs {
  background-color: #252526;
  width: 100%;
  color: #858690;
  display: flex;
  min-width: 20px;
  cursor: pointer;

}

.vs-code-tab-main {
  width: 100%;
}

.span-tab {
  max-width: calc(100% - 40px);
  /* the element needs a fixed width (in px, em, %, etc) */
  overflow: hidden;
  /* make sure it hides the content that overflows */
  white-space: nowrap;
  /* don't break the line */
  text-overflow: ellipsis;
  /* give the beautiful '...' effect */
}

section {
  max-height: 25px;
  border: solid 2px;
  border-color: #252526;
  display: flex;
}

.icon-white {
  max-width: 25px;
  max-height: 25px;
  padding: 3px;
  filter: invert(87%) sepia(1%) saturate(2773%) hue-rotate(258deg) brightness(110%) contrast(108%);
}

.tab-icon {
  max-width: 25px;
  max-height: 25px;
}

.vs-content {
  height: calc(100% - 49px);
  background-color: #1e1e1e;
  overflow-y: scroll;
  color: whitesmoke;
  padding: 10px;
}

.vs-content::-webkit-scrollbar {
  width: 14px;
}

.vs-content::-webkit-scrollbar-track {
  -webkit-box-shadow: solid 0 0 2px #383838;
  border-radius: 1px;
}

.vs-content::-webkit-scrollbar-thumb {
  border-radius: 1px;
  -webkit-box-shadow: inset 0 0 2px #e1e1e1;
}

.left {
  float: left;
  max-width: 25px;
  font-size: 16px;
  padding: 6px;
  text-align: center;
}

.center {
  color: #858690;
  font-size: 12px;
  padding: 10px;
  text-align: center;
}

.vs-code-left-tools {
  background-color: #333333;
  max-width: 50px;
  max-height: 100%;
  min-height: 100%;
}

@media only screen and (max-width: 768px) {

  /* For mobile phones: */
  section {
    max-height: 25px;
    border: solid 2px;
    display: flex;
    border: solid 2px;
    border-color: #252526;
  }

  .vs-code-tab-main {
    width: 100%;
    min-width: 100px;
  }

.vs-bottom-left-1{
  width: 110px;
}


}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}